import FundsTransferFrequencyService from '@/services/ft/FundsTransferFrequencyService'
import LoadingMixin from '@/mixins/commons/LoadingMixin'
import ListLayoutMixin from '@/mixins/commons/ListLayoutMixin'

let TransferFrequencyTableMixin = {
  mixins: [LoadingMixin, ListLayoutMixin],
  props: {
    defaultTransferFrequencyTableFilter: {
      type: String,
      default: null
    },
    defaultTransferFrequencyTableOrderBy: {
      type: String,
      default: null
    },
    frequencyBehaviourEnumId: {
      type: String,
      default: null
    }
  },
  data: function () {
    return {
      transferFrequencyTableData: [],
      transferFrequencyTableFilterTimer: null,
      transferFrequencyTableFilter: this.defaultTransferFrequencyTableFilter,
      transferFrequencyTableOrderBy: this.defaultTransferFrequencyTableOrderBy,
      transferFrequencyTableContainer: 'loading-container',
      transferFrequencyTableBusy: false,
      transferFrequencyTableTotalRows: 0,
      transferFrequencyTableCurrentPage: 1,
      transferFrequencyTablePerPage: 10
    }
  },
  computed: {
    transferFrequencyTableNoDataText: function () {
      return this.transferFrequencyTableBusy ? this.$t('LOADING') + ' ...' : this.$t('NO_DATA_FOUND')
    },
    transferFrequencyTableTotalPages: function () {
      return Math.ceil(this.transferFrequencyTableTotalRows / this.transferFrequencyTablePerPage)
    },
    transferFrequencyTablePagingInfo: function () {
      return this.pagingInfo(this.transferFrequencyTableCurrentPage, this.transferFrequencyTablePerPage, this.transferFrequencyTableTotalRows)
    }
  },
  methods: {
    loadTransferFrequencyTableData (loadingContainerId) {
      let self = this
      this.transferFrequencyTableBusy = true
      this.transferFrequencyTableContainer = loadingContainerId || this.transferFrequencyTableContainer
      this.startLoading(this.transferFrequencyTableContainer)
      FundsTransferFrequencyService.transferFrequencies({
        pageIndex: this.transferFrequencyTableCurrentPage - 1,
        pageSize: this.transferFrequencyTablePerPage,
        filter: this.transferFrequencyTableFilter,
        orderByField: this.transferFrequencyTableOrderBy,
        frequencyBehaviourEnumId: this.frequencyBehaviourEnumId
      }).then(function (response) {
        self.stopLoading(self.transferFrequencyTableContainer)
        self.transferFrequencyTableBusy = false
        self.transferFrequencyTableTotalRows = response.data.totalRows
        self.transferFrequencyTableData = response.data.transferFrequencyList || []
      }).catch(function (error) {
        self.stopLoading(self.transferFrequencyTableContainer)
        self.transferFrequencyTableBusy = false
        self.$emit('show-alert', 'danger', error.response.data.errors)
      })
    },
    refreshTransferFrequencyTable () {
      this.loadTransferFrequencyTableData()
    },
    filterTransferFrequencyTable (filter) {
      let self = this
      if (this.transferFrequencyTableFilter !== filter) {
        clearTimeout(this.transferFrequencyTableFilterTimer)
        this.transferFrequencyTableFilterTimer = setTimeout(() => {
          self.transferFrequencyTableFilter = filter
          self.loadTransferFrequencyTableData()
        }, process.env.VUE_APP_SEARCH_DELAY)
      } else {
        clearTimeout(this.transferFrequencyTableFilterTimer)
      }
    },
    orderTransferFrequencyTable (orderBy, active) {
      this.transferFrequencyTableOrderBy = (active ? '' : '-') + orderBy
      this.loadTransferFrequencyTableData()
    }
  },
  watch: {
    transferFrequencyTableCurrentPage: function () {
      this.refreshTransferFrequencyTable()
    },
    transferFrequencyTablePerPage: function () {
      this.refreshTransferFrequencyTable()
    },
    frequencyBehaviourEnumId: function () {
      this.refreshTransferFrequencyTable()
    }
  }
}

export default TransferFrequencyTableMixin
